import { Component, OnInit } from '@angular/core';

import { Router } from '@angular/router';
import { NgForm } from '@angular/forms';
import { LoginserviceService } from './loginservice.service';
import { Login } from './login';

@Component({
  selector: 'app-login',
  templateUrl: './login.component.html',
  styleUrls: ['./login.component.scss']
})
export class LoginComponent implements OnInit {
  passwordMatchValidator: any;
  sessionTokenUserName: string;
  sessionTokenRoleID: string;


 
  roleid: number;


  constructor( private router: Router,private service:LoginserviceService) { }
    // login = new Login();
    login:Login
  ngOnInit() {

    this.resetForm();
    
  }

  OnSubmit(form: NgForm) {
    this.validLogin(form);
  }
  resetForm(form?: NgForm) {
    if (form != null)
      form.resetForm();
    this.service.formData = {
      username: '',
      password: '' ,                                         
    },
      { validator: this.passwordMatchValidator }
  }
  validLogin(form: NgForm) {
    
    this.service.login(form.value).subscribe(data => {
      this.login = data;
      
      console.log(this.login.username);
      // window.alert(this.login.username);
      this.router.navigate(['vendor'])
      
      localStorage.setItem('isLoggedIn', "true");
      localStorage.setItem('token', data.userName);
      localStorage.setItem('tokenRoleId', data.userName);

     
    }, (error) => {
      console.log(error);
      window.alert("Wrong username or password");
    });
  }
  
  // logout() :void 
  // {    
  //   localStorage.setItem('isLoggedIn','false');    
  //   localStorage.removeItem('token');  
  //   localStorage.removeItem('tokenRoleId');
  //   this.router.navigate(['/']);   
  // }
}

    
    