import { Component, OnInit } from '@angular/core';
import { Vendor } from '../vendor';

import { ActivatedRoute, Router } from '@angular/router';
import { VendorService } from '../vendor.service';
import { AuthService } from 'src/app/service/auth.service';

@Component({
  selector: 'app-viewcontact',
  templateUrl: './viewcontact.component.html',
  styleUrls: ['./viewcontact.component.scss']
})
export class ViewcontactComponent implements OnInit {
  vendors:Vendor[];
  vendor=new Vendor();
  vId:any;
  constructor(private _service:VendorService,private route:ActivatedRoute,private router:Router,private authservice:AuthService) { }

  ngOnInit() {

    this.getVendors();
    this.resetform();

  }
  logout(){
    this.authservice.logout();
  }

 
 

  resetform() {
    this._service.vendor = {
      vendorId: null,
      vendorName: '',
      address: '',
      location: '',
      service: '',
      pincode: null,
      personId: null,
      personName: '',
      department: '',
      email: '',
      phone: '',
    }
  }

  getVendors(){
    this.vId=this.route.snapshot.paramMap.get('id');
    this._service.getVendorbyId(this.vId)
    .subscribe((vendordata)=>{
      this.vendor=vendordata;
      console.log(vendordata);

    },(error)=>{
      console.log(error);

    });
  }
  back(){
    this.router.navigate(['vendor']);
  }
  
  


}
