import { Injectable } from '@angular/core';
import { Observable } from 'rxjs';
import { Vendor } from './vendor';
import { HttpClient, HttpHeaders } from '@angular/common/http';
import { environment } from 'src/environments/environment';
import { Router } from '@angular/router';

@Injectable({
  providedIn: 'root'
})
export class VendorService {
  formData:Vendor;
  vendor:Vendor;
  vendors:Vendor;

  constructor(private _httpService:HttpClient, private router:Router) { }
 

  // to get all vendor details
  getAllVendors():Observable<Vendor[]>{
    return this._httpService.get<Vendor[]>
    (environment.APIURL+'/vendors');      
  }
  disableVendors(vendorId:any){
    let body = JSON.stringify(vendorId);
    let headers = new HttpHeaders({'Content-Type': 'application/json'});
    let options= { headers:headers}
    return this._httpService.put<Vendor[]>(environment.APIURL+'/disableVendor/'+vendorId,body,options);
  }
  getVendorbyId(vId:any): Observable<Vendor> {
    return this._httpService.get<Vendor>(environment.APIURL + '/person/' + vId)
  }
  logout(){
    this.router.navigate(['/login']);
  }



}
  



