import { Component, OnInit } from '@angular/core';
import { Router, ActivatedRoute } from '@angular/router';
import { SavevendorService } from './savevendor.service';
import { NgForm } from '@angular/forms';
import { Vendor } from './vendor';

@Component({
  selector: 'app-savevendor',
  templateUrl: './savevendor.component.html',
  styleUrls: ['./savevendor.component.scss']
})
export class SavevendorComponent implements OnInit {
  vendor = new Vendor();
  vId:any;
  

  constructor(private router: Router,private route:ActivatedRoute,private _service: SavevendorService) { }

  ngOnInit() {
   
  }
  back(){
    this.router.navigate(['vendor']);
  }

  resetform() {
    this._service.vendor = {
      vendorId:null,
      vendorName: '',
      address: '',
      location: '',
      service: '',
      pinCode: null,
      personName: '',
      department: '',
      email: '',
      phone: '',
    }
  }


  saveVendor() {
    this.resetform();
    this._service.insertVendor(this.vendor)
    .subscribe((response)=>{
      console.log(this.vendor);
      console.log(response);
      // this.toastr.success('Saved successfully');
      this.router.navigate(['vendor'])
    }, (error)=>{
      console.log(error);
    });
  }

 
}


