import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';
import { LoginComponent } from './login/login.component';
import { VendorListComponent } from './vendor-list/vendor-list.component';
import { SavevendorComponent } from './savevendor/savevendor.component';
import { EditvendorComponent } from './editvendor/editvendor.component';
import { ViewcontactComponent } from './vendor-list/viewcontact/viewcontact.component';
import { AuthGuard } from './guards/auth.guard';



const routes: Routes = [
  {
    path:'',
    redirectTo: 'login', pathMatch: 'full'
  },
  
  {
    //login Page
    
    path:'login',
    component:LoginComponent
  },

    // to add vendor
    {
      path:'vendor',
      component : VendorListComponent, canActivate:[AuthGuard]
      
    },
     //To edit vendor
  {
    path : 'savevendor/:id',
    component : EditvendorComponent,
  },
    {
      path:'saveVendor',
      component : SavevendorComponent
      
    },
    {
      path:'viewPerson/:id',
      component : ViewcontactComponent, canActivate:[AuthGuard]
      
    },
    
];

@NgModule({
  imports: [RouterModule.forRoot(routes)],
  exports: [RouterModule]
})
export class AppRoutingModule { 
  
}
