import { Component, OnInit } from '@angular/core';
import { Vendor } from './vendor';
import { Router, ActivatedRoute } from '@angular/router';
import { VendorService } from './vendor.service';
import { AuthService } from '../service/auth.service';


@Component({
  selector: 'app-vendor-list',
  templateUrl: './vendor-list.component.html',
  styleUrls: ['./vendor-list.component.scss']
})
export class VendorListComponent implements OnInit {

  vendors:Vendor[];
  vendor=new Vendor();
  searchId:any;
  vId:any;




  constructor(private router:Router,private _vendorService:VendorService,private route:ActivatedRoute,private authservice:AuthService) { }

  ngOnInit() {
    this. getVendors();
  }
   // to pass id
   PassId(searchVendor:any):void{
    this.searchId=searchVendor.vendorId;
  }
  logout(){
    this.authservice.logout();
  }

   // to view vendor details
   getVendors():void{
    this._vendorService.getAllVendors()
    .subscribe((vendordata)=>{
      this.vendors=vendordata;
      console.log(vendordata);

    },(error)=>{
      console.log(error);

    });
  }
   // to disable vendor
   DisableVendor(vid:any):void{
    this._vendorService.disableVendors(this.searchId)
    .subscribe((response)=>{
      console.log(response);
      this.getVendors();
    })
  }
  // redirect to view contact
  viewContact(personId:any){
    this.router.navigate(['viewPerson',personId]);
  }

  //get vendor by id
  getVendorById(){
    this.resetform();
    this.vId=this.route.snapshot.paramMap.get('id');
    this._vendorService.getVendorbyId(this.vId)
    .subscribe((data) =>{
      this.vendor=data;
      console.log('inside');
      console.log(data);
      
      console.log(this.vendor.vendorName);
    },(error)=>{
      console.log(error);
    })
  }
  resetform() {
    this._vendorService.vendor = {
      vendorId: null,
      vendorName: '',
      address: '',
      location: '',
      service: '',
      pincode: null,
      personId: null,
      personName: '',
      department: '',
      email: '',
      phone: '',
    }
  }



   // redirect to save vendor component to edit
   addVendor(id:any){
    this.router.navigate(['savevendor',id]);
  }
   // redirect to save vendor component to add 
   add1Vendor(){
    this.router.navigate(['savevendor']);
  }
  viewPerson(){
    this.router.navigate(['viewPerson']);
  }



  // logout(){
  //   this.service.logout();

  // }


}
