import { Component, OnInit } from '@angular/core';
import { ActivatedRoute, Router } from '@angular/router';
import { Vendor } from './vendor';
import { VendorService } from './vendor.service';

@Component({
  selector: 'app-editvendor',
  templateUrl: './editvendor.component.html',
  styleUrls: ['./editvendor.component.scss']
})
export class EditvendorComponent implements OnInit {
  constructor(private _service:VendorService,private route:ActivatedRoute,private router:Router) { }

  ngOnInit() {
    this.getVendorById();
    this.resetform();
  }

  vId:any;
  vendorName:any;
  vendor = new Vendor();
  // vendor:Vendor;

  resetform() {
    this._service.vendor = {
      vendorId: null,
      vendorName: '',
      address: '',
      location: '',
      service: '',
      pincode: '',
      personName: '',
      department: '',
      email: '',
      phone: '',
    }
  }
  back(){
    this.router.navigate(['vendor']);
  }

  getVendorById(){
    this.resetform();
    this.vId=this.route.snapshot.paramMap.get('id');
    this._service.getVendorbyId(this.vId)
    .subscribe((data) =>{
      this.vendor=data;
      console.log('inside');
      console.log(this.vendor);
      
      console.log(this.vendor.vendorName);
    },(error)=>{
      console.log(error);
    })
  }

  saveVendor() {
    this.resetform();
    this._service.updateVendor(this.vendor,this.vId)
    .subscribe((response)=>{
      
      console.log(response);
      // this.toastr.success('Saved successfully');
      this.router.navigate(['vendor'])
    }, (error)=>{
      console.log(error);
    });
  }



}
